import BubbleUI from "react-bubble-ui";
import "react-bubble-ui/dist/index.css";
import ChildCircle from "./ChildComponentCircle";
import { data } from "./data";
import "./styles.css";

export default function App() {
  const options = {
    size: 80,
    minSize: 20,
    gutter: 18,
    provideProps: true,
    numCols: 6,
    fringeWidth: 45,
    yRadius: 100,
    xRadius: 100,
    cornerRadius: 50,
    showGuides: false,
    compact: true,
    gravitation: 5
  };

  const childrenCircle = data?.map((data, i) => {
    return <ChildCircle />;
  });

  return (
    <>
      <BubbleUI options={options} className={"myBubbleUI"}>
        {childrenCircle}
      </BubbleUI>
    </>
  );
}
